@import url("https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;0,700;1,400&display=swap");
@import url("../normalize.css");
html {
  min-height: 100%;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.4) 0%, rgba(245, 243, 244, 0.4) 100%);
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: 50% 50%;
}

body {
  font-size: 16px;
}

.FpsConverter {
  font-family: "Rubik", sans-serif;
  padding-bottom: 4.5rem;
}

.FpsConverterHeader {
  box-sizing: border-box;
  background-image: linear-gradient(180deg, #f5f3f4 0%, #b1a7a6 100%);
  background-position: 50% 50%;
  background-repeat: no-repeat;
  box-shadow: 0 0 4px 4px rgba(177, 167, 166, 0.5);
}
@media (orientation: portrait) {
  .FpsConverterHeader {
    height: 6rem;
    background-size: 100% 100%;
  }
}
@media (orientation: landscape) {
  .FpsConverterHeader {
    height: 5rem;
    background-size: 100% 100%;
  }
}
@media (orientation: landscape) and (min-width: 992px) {
  .FpsConverterHeader {
    height: 8rem;
  }
}

.FpsConverterHeaderContent {
  height: 100%;
  box-sizing: border-box;
  margin: auto;
  max-width: 768px;
  display: flex;
  flex-direction: row;
  padding: 0 1.5rem;
  align-items: center;
  background-image: url(../img/header.svg);
  background-position: center right 1.5rem;
  background-repeat: no-repeat;
}
@media (orientation: portrait) {
  .FpsConverterHeaderContent {
    background-size: 50% auto;
  }
}
@media (orientation: landscape) {
  .FpsConverterHeaderContent {
    background-size: auto 95%;
  }
}

.FpsConverterTitle {
  flex-basis: calc(50% - 1rem);
  font-size: 1.25rem;
  padding: 0 4px;
  color: #fff;
}
@media (orientation: landscape) {
  .FpsConverterTitle {
    font-size: 1.5rem;
  }
}
.FpsConverterTitle SPAN {
  box-decoration-break: clone;
  background-color: #e5383b;
  box-shadow: -4px 0 0 #e5383b, 4px 0 0 #e5383b;
}

.FpsConverterSection {
  box-sizing: border-box;
  margin: auto;
  max-width: 768px;
  padding: 1.5rem;
  padding-bottom: 0;
}

.FpsConverterDescription {
  padding: 0;
}
.FpsConverterDescription h2 {
  font-size: 1.15rem;
  margin: 0;
  margin-bottom: 0.75rem;
}
.FpsConverterDescription h2:not(:first-child) {
  margin-top: 1.5rem;
}
.FpsConverterDescription p {
  font-size: 1rem;
  margin: 0;
  margin-bottom: 0.75rem;
}
.FpsConverterDescription em {
  text-decoration: underline;
  text-decoration-color: #E5383B;
  text-decoration-thickness: 2px;
}
.FpsConverterDescription span {
  display: inline-block;
  font-weight: 700;
  color: #fff;
  box-decoration-break: clone;
  background-color: #e5383b;
  padding: 0 4px;
}
.FpsConverterDescription span.FpsConverterDescriptionLink {
  cursor: pointer;
  text-decoration: underline;
  text-decoration-color: #fff;
  text-decoration-thickness: 2px;
}
.FpsConverterDescription span.FpsConverterDescriptionSpan {
  background-color: #A4161A;
}
.FpsConverterDescription[data-more=true] .FpsConverterDescriptionLess {
  display: none;
}
.FpsConverterDescription:not([data-more=true]) .FpsConverterDescriptionMore {
  display: none;
}

.CalculatorPanel {
  display: block;
  margin: 1.5rem 0;
  margin-bottom: 0;
  padding: 1rem;
  border-radius: 0.5rem;
  background-color: lightgray;
  transition: background-color 400ms;
}
.CalculatorPanel:first-child {
  margin-top: 0;
}
.FpsConverter[data-panel=power] .CalculatorPanel[data-panel=power] {
  background-color: #b1a7a6;
}
.FpsConverter[data-panel=velocity] .CalculatorPanel[data-panel=velocity] {
  background-color: #b1a7a6;
}
.FpsConverter[data-panel=weight] .CalculatorPanel[data-panel=weight] {
  background-color: #b1a7a6;
}

.CalculatorPanelForm {
  display: grid;
  gap: 1rem;
}
@media (orientation: portrait) {
  .CalculatorPanelForm {
    grid-template-columns: 1fr;
  }
}
@media (orientation: landscape) {
  .CalculatorPanelForm {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
}

.CalculatorPanelTitle {
  font-size: 1.25rem;
  margin: 0;
  font-weight: 700;
  margin-bottom: 0.5rem;
}
.CalculatorPanelTitle SPAN {
  display: inline-block;
  text-decoration: underline;
  text-decoration-color: #E5383B;
  text-decoration-thickness: 2px;
}

.CalculatorPanelSubtitle {
  font-size: 75%;
  font-style: italic;
  margin-bottom: 0.5rem;
}

.CalculatorPanelBlock {
  display: grid;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
}
@media (orientation: portrait) {
  .CalculatorPanelBlock {
    grid-template-columns: 1fr;
  }
}
@media (orientation: landscape) {
  .CalculatorPanelBlock {
    grid-template-columns: 1fr auto;
  }
}
.CalculatorPanelBlock[data-type=output] {
  background: #f5f3f4;
  grid-template-columns: auto auto auto;
  box-shadow: inset 0 0 8px 4px rgba(127, 127, 127, 0.4);
  border-radius: 0.5rem;
  padding: 0.5rem;
}
@media (orientation: landscape) {
  .CalculatorPanelBlock[data-type=output] {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

@media (orientation: landscape) {
  .CalculatorPanelBlock:not([data-type=output]) .CalculatorPanelLabel {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}

.CalculatorPanelUnit {
  display: block;
  flex-basis: 100%;
  font-size: 0.75rem;
  font-style: italic;
  color: #444;
}

.CalculatorPanelOutput {
  font-size: 1.25rem;
  font-weight: bold;
}
.CalculatorPanelBlock[data-type=output] .CalculatorPanelOutput:before {
  content: "= ";
  color: rgba(0, 0, 0, 0.4);
}

.SettingsPanel {
  box-sizing: border-box;
  position: fixed;
  bottom: 1rem;
  left: 0;
  width: 100%;
}

.SettingsPanelContent {
  box-sizing: border-box;
  margin: auto;
  max-width: 768px;
  padding: 0 1.5rem;
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
}

.MeasurementSelector, .LanguageSelector {
  cursor: pointer;
  display: inline-block;
  text-align: center;
  line-height: 2.5rem;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 0.5rem;
  background-color: #A4161A;
  box-shadow: 0 0 4px 2px rgba(127, 127, 127, 0.4);
}

.MeasurementSelector {
  background-image: url(../img/settings-icon-scale.svg);
  background-repeat: no-repeat;
  background-size: 80% auto;
  background-position: 50% 50%;
  line-height: 3rem;
}

.MeasurementSelectorItem, .LanguageSelectorItem {
  display: inline-block;
  color: #A4161A;
  font-size: 0.75rem;
  font-weight: 700;
}

.MeasurementSelectorItem {
  color: #A4161A;
}

.LanguageSelectorItem {
  color: #fff;
}

.MeasurementSelector[data-measurement=metric] .MeasurementSelectorItem[data-measurement=metric] {
  display: none;
}

.MeasurementSelector[data-measurement=imperial] .MeasurementSelectorItem[data-measurement=imperial] {
  display: none;
}

.LanguageSelector[data-lang=HU] .LanguageSelectorItem[data-lang=HU] {
  display: none;
}

.LanguageSelector[data-lang=EN] .LanguageSelectorItem[data-lang=EN] {
  display: none;
}

