@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;0,700;1,400&display=swap');
@import url('../normalize.css');

$panels: 'power', 'velocity', 'weight';
$languages: 'HU', 'EN';
$measurements: 'metric', 'imperial';

html {
    min-height: 100%;
    background-image: linear-gradient(180deg, rgba(255,255,255,.4) 0%, rgba(245, 243, 244,.4) 100%);
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: 50% 50%;
}
body {
    font-size: 16px;
}

.FpsConverter {
    font-family: 'Rubik', sans-serif;
    padding-bottom: 4.5rem;
}

.FpsConverterHeader {
    box-sizing: border-box;
    background-image: linear-gradient(180deg, rgba(245,243,244,1) 0%, rgba(177,167,166,1) 100%);
    background-position: 50% 50%;
    background-repeat: no-repeat;
    box-shadow: 0 0 4px 4px rgba(177, 167, 166, .5);

    @media (orientation: portrait) {
        height: 6rem;
        background-size: 100% 100%;
    }
    @media (orientation: landscape) {
        height: 5rem;
        background-size: 100% 100%;

        @media (min-width: 992px) {
            height: 8rem;
        }
    }
}
.FpsConverterHeaderContent {
    height: 100%;
    box-sizing: border-box;
    margin: auto;
    max-width: 768px;
    display: flex;
    flex-direction: row;
    padding: 0 1.5rem;
    align-items: center;

    background-image: url(../img/header.svg);
    background-position: center right 1.5rem;
    background-repeat: no-repeat;

    @media (orientation: portrait) {
        background-size: 50% auto;
    }
    @media (orientation: landscape) {
        background-size: auto 95%;
    }
}
.FpsConverterTitle {
    flex-basis: calc(50% - 1rem);
    font-size: 1.25rem;
    padding: 0 4px;
    color: #fff;

    @media (orientation: landscape) {
        font-size: 1.5rem;
    }

    SPAN {
        box-decoration-break: clone;
        background-color: rgba(229, 56, 59,1);
        box-shadow: -4px 0 0 rgba(229, 56, 59,1), 4px 0 0 rgba(229, 56, 59,1);
    }
}

.FpsConverterSection {
    box-sizing: border-box;
    margin: auto;
    max-width: 768px;
    padding: 1.5rem;
    padding-bottom: 0;
}

.FpsConverterDescription {
    padding: 0;

    h2 {
        font-size: 1.15rem;
        margin: 0;
        margin-bottom: .75rem;

        &:not(:first-child) {
            margin-top: 1.5rem;
        }
    }
    p {
        font-size: 1rem;
        margin: 0;
        margin-bottom: .75rem;
    }
    em {
        text-decoration: underline;
        text-decoration-color: #E5383B;
        text-decoration-thickness: 2px;
    }
    span {
        display: inline-block;
        font-weight: 700;
        color: #fff;
        box-decoration-break: clone;
        background-color: rgba(229, 56, 59,1);
        padding: 0 4px;

        &.FpsConverterDescriptionLink {
            cursor: pointer;
            text-decoration: underline;
            text-decoration-color: #fff;
            text-decoration-thickness: 2px;
        }
        &.FpsConverterDescriptionSpan {
            background-color: #A4161A;
        }
    }

    &[data-more=true] {
        .FpsConverterDescriptionLess { display: none; }
    }
    &:not([data-more=true]) {
        .FpsConverterDescriptionMore { display: none; }
    }
}

.CalculatorPanel {
    display: block;
    margin: 1.5rem 0;
    margin-bottom: 0;
    padding: 1rem;
    border-radius: .5rem;
    background-color: rgba(211,211,211,1);
    transition: background-color 400ms;

    &:first-child { margin-top: 0; }

    @each $panel in $panels {
        .FpsConverter[data-panel="#{$panel}"] &[data-panel="#{$panel}"] {
            background-color: rgba(177,167,166,1);
        }
    }
}
.CalculatorPanelForm {
    display: grid;
    gap: 1rem;

    @media (orientation: portrait) {
        grid-template-columns: 1fr;
    }
    @media (orientation: landscape) {
        grid-template-columns: 1fr 1fr;
        gap: 1rem;
    }
}
.CalculatorPanelTitle {
    font-size: 1.25rem;
    margin: 0;
    font-weight: 700;
    margin-bottom: .5rem;

    SPAN {
        display: inline-block;
        text-decoration: underline;
        text-decoration-color: #E5383B;
        text-decoration-thickness: 2px;
    }
}
.CalculatorPanelSubtitle {
    font-size: 75%;
    font-style: italic;
    margin-bottom: .5rem;
}
.CalculatorPanelBlock {
    display: grid;
    align-items: center;
    justify-content: center;
    gap: .25rem;

    @media (orientation: portrait) {
        grid-template-columns: 1fr;
    }
    @media (orientation: landscape) {
        grid-template-columns: 1fr auto;
    }

    &[data-type=output] {
        background: rgba(245,243,244,1);
        grid-template-columns: auto auto auto;
        box-shadow: inset 0 0 8px 4px rgb(127,127,127,.4);
        border-radius: .5rem;
        padding: .5rem;

        @media (orientation: landscape) {
            grid-column-start: 1;
            grid-column-end: 3;
        }
    }
}
.CalculatorPanelLabel {
    @media (orientation: landscape) {
        .CalculatorPanelBlock:not([data-type=output]) & {
            grid-column-start: 1;
            grid-column-end: 3;
        }
    }
}
.CalculatorPanelInput {
}
.CalculatorPanelUnit {
    display: block;
    flex-basis: 100%;
    font-size: .75rem;
    font-style: italic;
    color: #444;
}
.CalculatorPanelOutput {
    font-size: 1.25rem;
    font-weight: bold;

    .CalculatorPanelBlock[data-type=output] &:before {
        content: '= ';
        color: rgba(0,0,0,.4);
    }
}

.SettingsPanel {
    box-sizing: border-box;
    position: fixed;
    bottom: 1rem;
    left: 0;
    width: 100%;
}
.SettingsPanelContent {
    box-sizing: border-box;
    margin: auto;
    max-width: 768px;
    padding: 0 1.5rem;
    display: flex;
    gap: 1rem;
    justify-content: flex-end;
}

.MeasurementSelector, .LanguageSelector {

    cursor: pointer;
    display: inline-block;
    text-align: center;

    line-height: 2.5rem;
    width: 2.5rem; height: 2.5rem;
    border-radius: .5rem;
    background-color: #A4161A;
    box-shadow: 0 0 4px 2px rgb(127,127,127,.4);

}
.MeasurementSelector {
    background-image: url(../img/settings-icon-scale.svg);
    background-repeat: no-repeat;
    background-size: 80% auto;
    background-position: 50% 50%;
    line-height: 3rem;
}
.MeasurementSelectorItem, .LanguageSelectorItem {
    display: inline-block;
    color: #A4161A;
    font-size: .75rem;
    font-weight: 700;
}
.MeasurementSelectorItem {
    color: #A4161A;
}
.LanguageSelectorItem {
    color: #fff;
}

@each $measurement in $measurements {
    .MeasurementSelector[data-measurement="#{$measurement}"] .MeasurementSelectorItem[data-measurement="#{$measurement}"] {
        display: none;
    }
}

@each $language in $languages {
    .LanguageSelector[data-lang="#{$language}"] .LanguageSelectorItem[data-lang="#{$language}"] {
        display: none;
    }
}
